

const BASE_URL = "http://localhost:3000/scps";

async function scpFunc() {
  try {
    const response = await fetch(
      `${BASE_URL}`
    );
    const result = await response.json();
    return result;
  } catch (error) {
    console.log(error);
  }
}

scpFunc().then((data) => {
  const scpList = document.querySelector(".scp__list");
  data.forEach((item) => {
    const scpItem = document.createElement("li");
    scpItem.classList.add("scp__item");
    scpItem.innerHTML = `
      <h2 class="scp__title">${item.title}</h2>
      <a href="${item.url}" class="scp__link">Read more</a>
      <button class="scp__delete" data-id="${item.id}">Delete</button>
    `;
    scpList.appendChild(scpItem);
  }); 
  const deleteButtons = document.querySelectorAll(".scp__delete");
  deleteButtons.forEach((button) => {
    button.addEventListener("click", async (event) => {
      const id = event.target.getAttribute("data-id");
      try {
        const response = await fetch(`${BASE_URL}/${id}`, {
          method: "DELETE",
        });
        if (response.ok) {
          event.target.closest(".scp__item").remove();
        } else {
          console.error("Failed to delete item");
        }
      } catch (error) {
        console.error("Error deleting item:", error);
      }
    });
  });
});

